import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    usuario: {
      usuario: '',
      pin: '',
      ramal:'',
      fila: '',
    } ,
    gravacaoAtual: null,

    
    token: null

  },
  getters: {
  },
  mutations: {
    setPin(state, pay){
      state.usuario = pay

    },
    setGravacaoAtual(state, pay){
      state.gravacaoAtual = pay
    }
  },
  actions: {
    guardaPin(state, pay){
      state.commit('setPin', pay)
    },
    insereGravacaoAtual(state, pay){
      state.commit("setGravacaoAtual", pay)
    }
  },
  modules: {
  }
})
